<template>
  <g
    tabindex="0"
    class="vis-node"
    :class="{ 'connector-hover': configure.connector.hover && !dragging }"
    :width="size.width"
    :height="size.height"
    :transform="`translate(${position.x}, ${position.y})rotate(${rotate}, ${anchor.x} ${anchor.y})`"
    @mousedown.stop="mousedown"
  >
    <component :is="framework.shape" :size="size" v-bind="shape"></component>

    <component :is="framework.widget" :size="size" v-bind="widget"></component>

    <component
      :class="{ 'connector-transparent': configure.connector.hover }"
      ref="connector"
      :is="framework.connector"
      :node="id"
      :position="position"
      :anchor="anchor"
      :size="size"
      v-bind="connector"
    ></component>
  </g>
</template>

<script>
import svg from "../../mixins/svg";
import node from "../../mixins/node";
import dev from "../../mixins/dev";

import { components as shapeComponents } from "./shape";
import { components as connectorComponents } from "./connector";
import { components as widgetComponents } from "./widget";
export default {
  mixins: [svg, node, dev],
  components: {
    ...shapeComponents,
    ...connectorComponents,
    ...widgetComponents,
  },
  props: {
    framework: {
      type: Object,
    },
    translate: {
      type: Object,
      default: () => {
        return {
          x: 0,
          y: 0,
        };
      },
    },
    rotate: {
      type: Number,
      default: 0,
    },
    anchor: {
      type: Object,
      default: () => {
        return {
          x: 25,
          y: 25,
        };
      },
    },
    size: {
      type: Object,
      default: () => {
        return {
          width: 50,
          height: 50,
        };
      },
    },
    configure: {
      type: Object,
      default: () => {
        return {
          connector: { hover: true },
        };
      },
    },
    widget: {
      type: Object,
    },
    shape: {
      type: Object,
    },
    connector: {
      type: Object,
    },
    line: {
      type: Object,
      default: () => {
        return {
          component: "svgline",
          fill: "white",
          stroke: "black",
          strokeWidth: 2,
          cursor: "pointer",
        };
      },
    },
  },

  data() {
    return {
      dragging: false,
      // 
      dragStartPointer: {
        x: 0,
        y: 0,
      },
      // translate
      cacheTranslate: {
        x: 0,
        y: 0,
      },
    };
  },

  computed: {
    position() {
      return {
        x: this.translate.x - this.anchor.x,
        y: this.translate.y - this.anchor.y,
      };
    },

    canvas() {
      return this.$store.getters["canvas/canvas"];
    },

    dev() {
      return this.$store.getters.mode === "dev";
    },
  },

  methods: {
    mousedown(event) {
      this.dragging = true;

      const dragStartPointer = this.dragStartPointer;
      const cacheTranslate = this.cacheTranslate;
      const translate = this.translate;
      const canvas = this.canvas;

      cacheTranslate.x = this.translate.x;
      cacheTranslate.y = this.translate.y;

      dragStartPointer.x = event.clientX;
      dragStartPointer.y = event.clientY;

      const mousemoveFun = (event) => {
        translate.x = cacheTranslate.x + event.clientX - dragStartPointer.x;
        translate.y = cacheTranslate.y + event.clientY - dragStartPointer.y;
      };

      const mouseupFun = () => {
        this.dragging = false;
        canvas.removeEventListener("mousemove", mousemoveFun);
        canvas.removeEventListener("mouseup", mouseupFun);
      };

      canvas.addEventListener("mousemove", mousemoveFun);
      canvas.addEventListener("mouseup", mouseupFun);
    },
  },
};
</script>

<style lang="less" scoped>
.vis-node {
  cursor: move;
}

.connector-transparent {
  opacity: 0;
  transition: all ease 300ms;
}

.connector-hover {
  &:hover .connector-transparent {
    opacity: 1;
  }
}
</style>
